import {useState, useEffect} from 'react';
import Slider from '../components/Slider';
import './style/NewsList.css';
import { useLocation } from 'react-router-dom';

function SearchResult(){
    const location = useLocation();
    const rawSearchData = location.state?.data || ''; //   (optional chaining )
    console.log('rawSearchData:', rawSearchData);
    const searchData = rawSearchData.trim(); //  
    let [searchPostData, setSearchPostData] = useState([]);
    let [loading, setLoading] = useState(true);
    
    useEffect(() => {
      if (!searchData) {
        //    
        console.log('  .');
        setSearchPostData([]); //     
        setLoading(false); //  
        return;
      }
      setLoading(true); //   
        fetch('/searchPost?searchData=' + searchData)
            .then(response => response.json())
            .then(data => {           
              if (data && Array.isArray(data)) {  // :   
                console.log(' :', data);
                setSearchPostData(data);
            } else {
                console.error('   :', data);
                setSearchPostData([]);
            }
            })
            .catch(error => {
            console.error('fetch :', error);
            setSearchPostData([]);
            })
            .finally(() => setLoading(false)); //         
    }, [searchData]);
    
    if (loading) {
      return <div> ...</div>;
    }

    if (!searchPostData.length) {
      return <div>  .</div>;
    }


    return (
    <div>
      <div>
        <div className="Recommend-Title">
          {searchData}   .
        </div>
        <div className="Recommend-Carousel">
          <div className="Recommend-Container">
            <Slider data={searchPostData}/>
          </div>
        </div>
      </div>
      <div>
        <div className="Recommend-Title">
          ,   
        </div>
        <div className="Recommend-Container"></div>
      </div>
    </div>
    );
}

export default SearchResult;