import {useState, useEffect} from 'react';
import './style/SignUp.css';

function SignUp(){
    let [new_userName, setUserName]=useState(''); //   
    let [new_userPassword, setUserPassword]=useState('');
    let [new_userNickName, setUserNickName]=useState('');
    
    function SignUp_MINIQUE(){
        fetch('/signUp-POST', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ nickname: new_userNickName ,username: new_userName, password: new_userPassword })
        })
        .then(response => response.json())
        .then(data => {
        console.log(' :', data);
        })
        .catch(error => {
        console.error('fetch :', error);
        });
    }
    return(
        <div>
            <div className='SignUp-Title'> </div>
            <input onChange={(e) => {
                setUserNickName(e.target.value);
            }} type="text"/>

            <input onChange={(e) => {
                setUserName(e.target.value);
            }} type="text"/>
            
            <input onChange={(e) => {
                setUserPassword(e.target.value);
            }} type="text"/>
            
            <button onClick={SignUp_MINIQUE}></button>
        </div>
    );
}

export default SignUp;