import './style/Header.css';
import SearchBar from './SearchBar';
import Image from 'react-bootstrap/Image';
import {Link} from 'react-router-dom';
import {useEffect, useState} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { changeLogIn, changeIsOpen } from "../store/store.js";

function Header() {
  let isLoggedIn = useSelector((state) => {return state.isLoggedIn}) //    
  let isOpen = useSelector((state) => {return state.isOpen})
  let dispatch = useDispatch();
  
  useEffect(() => {
      //      
      const checkLoginStatus = async () => {
        try {
          const response = await fetch('/checkLogin');
          const result = await response.json();
          console.log('   : ', result)
          //       
          dispatch(changeLogIn(result)); //   (true/false)
        } catch (error) {
          console.error('     :', error);
          dispatch(changeLogIn(false)); //       
        }
      };
        checkLoginStatus(); //      
        console.log('isLoggedIn  :', isLoggedIn);
  }, [isLoggedIn]); 
  
  function handleLogin(){
    dispatch(changeIsOpen(true));
  }

  async function handleLogOut(){
    try {
      //   
      const response = await fetch('/logOut', {
        method: 'POST',
        credentials: 'same-origin', //    
      });

      if (response.ok) {
        dispatch(changeLogIn(false));
      } else {
        alert(' ');
      }
    } catch (error) {
      console.error('   :', error);
      alert(' ');
    }
  }

  return (
    <header className='Header'>
      <div className='Header-Top'>
        <div style={{ flexGrow: 1 }}></div>
        <div className='Header-Top-Right'>
          <div className='Header-Top-Buttons'>
          {(isLoggedIn) ?
            <div onClick={handleLogOut} style={{cursor: 'pointer'}}></div> :
            <Link onClick={handleLogin} style={{textDecoration: 'none', color: '#212120'}}></Link>
          }
          </div>
          <div className='Header-Top-Buttons'> </div>
        </div>
      </div>
      <div className='Header-First'>
        <div className='Header-First-Logo'>
          <a href="/">
            <Image src="./img/Logo_horizontal.svg" />
          </a>
        </div>
        <div className='Header-First-SearchBar'>
          <SearchBar />
        </div>
        <div className='Header-First-Menu'>
          <div className='Header-First-Menu-Buttons'>
            <Link to="/" style={{textDecoration: 'none', color: 'black'}}></Link>
          </div>
          <div className='Header-First-Menu-Buttons'>
          {(isLoggedIn) ?
            <Link to="/write" style={{textDecoration: 'none', color: 'black'}}></Link> : 
            <Link onClick={handleLogin} style={{textDecoration: 'none', color: 'black'}}></Link>}
          </div>
          <div className='Header-First-Menu-Buttons'>
            <Link to="/" style={{textDecoration: 'none', color: 'black'}}> </Link>
          </div>
        </div>
      </div>
      <div className='Header-Second-Menu'>
        <div className='Header-Second-Menu-Buttons'>BEST</div>
        <div className='Header-Second-Menu-Buttons'>GIRLS</div>
        <div className='Header-Second-Menu-Buttons'>BOYS</div>
      </div>
    </header>
  );
}

export default Header;