import {useState} from 'react';
import { Link , useNavigate} from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { changeLogIn } from "../store/store.js";

function Login(){
    let [input_userName, setUserName]=useState(''); //   
    let [input_userPassword, setUserPassword]=useState('');

    let navigate = useNavigate();
    let dispatch = useDispatch();

    function handleLogin(){
        console.log("LoginMINIQUE  ")
        fetch('/login-POST', {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({ username: input_userName, password: input_userPassword}),
        withCredentials: true 
        })
        .then(response => response.json())
        .then(data => {
        console.log(' :', data);
        dispatch(changeLogIn(true));
        navigate(-1);
        })
        .catch(error => {
        console.error('fetch :', error);
        });
    }

    return(
        <>
            <div></div>
            <input onChange={(e) => {
                setUserName(e.target.value);
            }} type="text"/>
            
            <input onChange={(e) => {
                setUserPassword(e.target.value);
            }} type="text"/>
            
            <button onClick={handleLogin}></button>
            <button onClick={console.log('  .')}>
                <Link to= "/signUp" style={{textDecoration: 'none', color: '#212120'}}>
                    
                </Link>
            </button>
        </>
    );
}

export default Login;