import { useParams, Link, useNavigate } from "react-router-dom";
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Card from '../components/Card.js';
import { ButtonMedium, BuyButton, DeleteButtonHalf } from '../components/Buttons';
import './style/Detail.css';

function Detail() {
  let [pageResult, setPageResult] = useState([]);
  let [pageLike, setPageLike] = useState(0);
  let isLoggedIn = useSelector((state) => {return state.isLoggedIn})
  let [isLiked, setIsLiked] = useState(false);
  let navigate = useNavigate();
  let { id } = useParams();

  
  //          
  useEffect(()=>{
    //    ,     false 
    if(isLoggedIn) {
      fetch('/isLikedPost/' + id)
      .then((response) => response.json())
      .then((data) => {
        console.log('  ?:', data)
        setIsLiked(data);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      });
    } else {
      setIsLiked(false)
    }
  }, [id])

  useEffect(() => {
    //    
    const fetchData = async () => {
      try {
        const response = await fetch('/detail/' + id);
        const result = await response.json();
        setPageResult(result);
        setPageLike(result.like);
      } catch (error) {
        //  
        console.error('  :', error)
      }
    } 
    fetchData(); //   
  }, [id]); // id   
  
  function handleDelete() {
    // eslint-disable-next-line no-restricted-globals
    if (confirm(' ?    .')) {
      fetch('/delete/' + id, 
      {
        method : 'DELETE',
      })
      .then((response) => response.json())
      .then((data) => {
        if(data == true)
          alert("  .")
        else 
          alert("   .")
        navigate('/');
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      });
    }
  }



  function HeartON() {
    if(!isLoggedIn) {
      alert('     ')
      return
    }
    setIsLiked(true);
    setPageLike(pageLike + 1)
    fetch(('/like/' + id), {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      })
      .then(response => response.json())
      .then(data => {
      console.log(' :', data);
      })
      .catch(error => {
      console.error('fetch :', error);
      });
    }

  function HeartOFF() {
    if(!isLoggedIn) {
      alert('     ')
      return
    }
    setIsLiked(false);
    setPageLike(pageLike - 1)
    fetch(('/unlike/' + id), {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      })
      .then(response => response.json())
      .then(data => {
      console.log(' :', data);
      })
      .catch(error => {
      console.error('fetch :', error);
      });
  }


  
  return (
    <div>
      <div className="Detail-Container">
      <div className="Detail-Image">
        {pageResult.productPhoto && pageResult.productPhoto.length > 0 ? (
          <div>
            <img src={pageResult.productPhoto[0]} className="Detail-Image cover" alt=" " />
          </div>
        ) : (
          <div>   </div>
        )}
      </div>
        <div className="Detail-Content">
          <div className="Detail-FirstLine">
            <div className="Detail-Title">
            {pageResult.productName}
            </div>
            <div style={{flexGrow:1}}/>
            <div className="HeartBox">
              <div>
                { (isLiked) ? 
                <div onClick={()=>{HeartOFF()}} style={{fontSize: '20px', color: '#DB4437' , cursor: 'pointer'}}></div> 
                : 
                <div onClick={()=>{HeartON()}} style={{fontSize: '20px', color: '#212120', cursor: 'pointer'}}></div>
                }
              </div>
              <div>
                {pageLike}
              </div>
            </div>
          </div>
          <div className="Detail-Price">
            {pageResult.productPrice}
          </div>
          <div className="Detail-Content">
            {pageResult.productDetailContent}
          </div>
          <div className="Detail-Info">
            <div className="Detail-Info-Title">
              
            </div>
            <div className="Detail-Info-Content">
              {pageResult.productQuality}
            </div>
          </div>
          <div className="Detail-Info">
            <div className="Detail-Info-Title">
               
            </div>
            <div className="Detail-Info-Content">
              {pageResult.childAge}
            </div>
          </div>
          <div className="Detail-Info">
            <div className="Detail-Info-Title">
              
            </div>
            <div className="Detail-Info-Content">
                
            </div>
          </div>
          <div className="Detail-Info">
            <div className="Detail-Info-Title">
              
            </div>
            <div className="Detail-Info-Content">
              3500 {pageResult.user_id}
            </div>
          </div>
          <Link to={'/store/'+ pageResult.user_id } style={{textDecoration: 'none', color: 'black'}}>
            <div className="Detail-UserBox">
              <div>
                <div className="Detail-UserBox-Username">
                  {pageResult.username}
                </div>
                <div className="Detail-UserBox-Userinfo">
                  <div style={{color: '#FFBE64', marginRight: '8px'}}>
                    
                  </div>
                  <div>  
                    9.5
                  </div>
                  <div style={{marginLeft: '14px', marginRight:'14px'}}>
                    |
                  </div>
                  <div>
                     
                  </div>
                </div>
              </div>
              <div className="Detail-UserBox-Follow">
                + 
              </div>
            </div>
          </Link>
          {(isLoggedIn) ? 
          <div style={{display: "flex", justifyContent: 'space-between', alignItems: 'center'}}>
            <DeleteButtonHalf eventHandler={handleDelete}/>
            <ButtonMedium text={''} eventHandler={()=>{navigate('/edit/'+id)}}/>
          </div>
          : <BuyButton/> }
        </div>
      </div>

      <div style={{display: 'block', fontSize: '20px', fontFamily: 'NotoSansKR-Medium', marginTop: '180px'}}>
          
      </div>
      <div class="Detail-Container">
        <Card photo={''}brand={'SainLaurant'} title={'1'} size={'62'} price={'1'}/>
        <Card photo={''}brand={'Gucci'} title={'2'} size={'62'} price={'2'}/>
        <Card photo={''}brand={'Tesla'} title={'3'} size={'62'} price={'3'}/>
        <Card photo={''}brand={'Apple'} title={'4'} size={'62'} price={'4'}/>
      </div>
    </div>
  );
}
export default Detail;